import * as React from "react";
import { useForm, SubmitHandler } from "react-hook-form";
import Layout from "../components/layout/Layout";
import Container from "react-bootstrap/Container";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import { Typeahead } from "react-bootstrap-typeahead";
import Parse, { runCloudFunction } from "../vendor/ParseApp";
import { getPdfText } from "../helpers/pdf";
import { countries } from "../helpers/country";
import AppContext from "../context/AppContext";

enum GenderEnum {
  female = "female",
  male = "male",
  other = "other",
}

interface IFormInput {
  firstName: String;
  gender: GenderEnum;
}

export default function App() {
  const { useEffect, useContext } = React;

  const options = ["tesst", "123"];
  const [file, setFile] = React.useState(null);
  const [model, setModel] = React.useState({
    title: "CV",
    tags: [],
    location: [{ id: "SG", label: "Singapore" }],
  });

  const setModelValue = (name, value) => {
    setModel({
      ...model,
      [name]: value,
    });
    console.log(model);
  };

  const context = useContext(AppContext);

  useEffect(() => {
    context.showLoading(true);
    runCloudFunction("getMyApplication").then((app) => {
      if(app) {
        setModel(app.get("data"));
      }
      context.showLoading(false);
    });
  }, [context.showLoading]);

  const isModelValid = () => {
    return model?.location?.length && model?.tags?.length && model.consent;
  };
  return (
    <>
      <Container className="">
        <Row>
          <Col>
            <Form.Group>
              <Form.Label>Seeking Job</Form.Label>
              <Typeahead
                allowNew={true}
                id="basic-typeahead-multiple"
                labelKey="name"
                multiple
                onChange={(d) => setModelValue("tags", d)}
                options={options}
                placeholder="Type in or select from  the list of jobs you are looking for"
                selected={model.tags}
              />
            </Form.Group>
          </Col>
        </Row>

        <Row>
          <Col>
            <Form.Group
              className="mb-3"
              controlId="exampleForm.ControlTextarea1"
            >
              <Form.Group>
                <Form.Label>Location to work in</Form.Label>
                <Typeahead
                  allowNew={true}
                  id="basic-typeahead-multiple"
                  labelKey="label"
                  multiple
                  selected={model.location}
                  onChange={(d) => setModelValue("location", d)}
                  options={countries}
                  placeholder="Type in or select from  the list"
                />
              </Form.Group>
            </Form.Group>
          </Col>
          <Col>
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
              <Form.Label>Citizenship</Form.Label>
              <Form.Control type="text" placeholder="" value={model.citizenship}  onChange={(d) => setModelValue("citizenship", d.target.value)}/>
            </Form.Group>
          </Col>
        </Row>

        <Row>
          <Col>
            <Form.Group
              className="mb-3"
              controlId="exampleForm.ControlTextarea1"
            >
              <Form.Label>Resume CV</Form.Label>
              <Form.Control
                name="document"
                type="file"
                rows={3}
                onChange={async (e) => {
                  setFile(e.target.files[0]);
                }}
              />

              {model.docUrl ? (
                <a className="color-brand-1" href={model.docUrl}>
                  {" "}
                  Download File{" "}
                </a>
              ) : (
                ""
              )}
            </Form.Group>
          </Col>
        </Row>

        <Row>
          <Col>
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
              <Form.Label>Expected Salary</Form.Label>
              <Form.Control
                type="text"
                placeholder=""
                value={model.expectSalary}
                onChange={(e) => setModelValue("expectSalary", e.target.value)}
              />
            </Form.Group>
          </Col>

          <Col>
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
              <Form.Label>Notice Period</Form.Label>
              <Form.Control
                type="text"
                placeholder=""
                value={model.noticePeriod}
                onChange={(e) => setModelValue("noticePeriod", e.target.value)}
              />
            </Form.Group>
          </Col>
        </Row>

        <Form>
          <Form>
            <Form.Check
              type="switch"
              id="custom-switch"
              label="Connect Me with Recruiters"
              checked={model.consent}
              onChange={() => {
                setModelValue("consent", !model.consent);
              }}
            />
          </Form>

          <Button
            variant="primary"
            onClick={async () => {
              context.showLoading(true);
              if (file) {
                const parseFile = new Parse.File("document", file);
                const document = await parseFile.save();
                const data = await document.getData();
                const output = await getPdfText(atob(data));
                const url = document.url();

                const newModel = {
                  ...model,
                  docUrl: url,
                  documentData: output,
                };
                setModel(newModel);
                await runCloudFunction("updateApplication", {
                  model: {
                    data: newModel,
                  },
                });
              } else {
                await runCloudFunction("updateApplication", {
                  model: {
                    data: model,
                  },
                });
              }

              context.showLoading(false);
            }}
          >
            Update
          </Button>
        </Form>
      </Container>
    </>
  );
}

export const Head: HeadFC = () => (
  <>
    <title>Staart</title>
    <script src="/assets/pdf/pdf.js" type="text/javascript" />
  </>
);
